/* src/components/Home.css */

body {
    font-family: 'Roboto Mono', monospace;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;

	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100vh;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
} 

  .hero {
    background-color: transparent;
    color: #000000;
    padding: 40px;
    text-align: center;
    border-radius: 10px;
  }
  
  .hero h1 {
    font-size: 2.5rem;
    margin-bottom: 15px;
  }
  
  .hero p {
    font-size: 1.2rem;
  }
  
  .about-me {
    margin-top: 30px;
  }
  
  .footer {
    margin-top: 30px;
    text-align: center;
    color: #fff;
  }
  
