/* src/components/Navbar.css */

body {
    font-family: 'Roboto Mono', monospace;
    /* margin-top: 10px; */
    overflow: hidden;
  }

.navbar {
    /* background-color: rgba(76, 147, 192, 0.7);  */
    padding: 0px;
    margin-top: 5px;
    margin-bottom: 0px;
    margin-left: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #ecf0f1;
    position: relative;
}

.logo {
    font-size: 1rem;
    display: flex;
    align-items: center;
    font-weight: bold;
    margin-left: 5px;
    margin-right: 10px;
  }

.logo img{
    height: 20px;
}
  
  .nav-links {
    list-style: none;
    display: flex;
    margin-top: 4px;
    margin-bottom: 5px;
    margin-left: 0px;
    padding-left: 0px;
  }
  
  .nav-links li {
    margin-right: 4px;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #fff;
    padding: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    /* border: solid rgb(255, 255, 255) 1px; */
    background-color: rgba(45, 13, 69, 0.2);
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.nav-links a:hover {
    background-color: rgba(255, 255, 255, 0.3);
    color: rgb(0, 0, 0);
}

.active {
    background-color: rgba(255, 255, 255, 0.8) !important;
    font-weight: bold;
    color: #000000 !important;
  }
    
  .inactive {
    background-color: yellow;
  }